@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home,
.reports,
.products {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
:root {
    --bg: #060b26;
    --bg-accent: #1b2246;
    --text-color: #f5f5f5;
    --nav-size: 60px;
    --border: 1px solid #632ce4;
    --border-radius: 8px;
    --speed: 500ms;
    --bg-btn: #632ce4;
}

.navbar {
    background-color: #060b26;
    background-color: var(--bg);
    height: 60px;
    height: var(--nav-size);
    border-bottom: 1px solid #632ce4;
    border-bottom: var(--border);
    display: flex;
    align-items: center;
    justify-content: start;
    position: relative;
    font-family: 'Lato', sans-serif;
}

.navbar-logo {
    margin-left: 2rem;
    font-size: 1.5rem;
    text-decoration: none;
    color: #f5f5f5;
    color: var(--text-color);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -8px;
}

.user-profile {
    margin-left: 1rem;
    font-size: 1.5rem;
    text-decoration: none;
    color: #f5f5f5;
    color: var(--text-color);
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar-search {
    font-size: 1.5rem;
    text-decoration: none;
    color: #f5f5f5;
    color: var(--text-color);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 30px;
    top: 18%;
}

.navbar-search-1 {
    font-size: 2rem;
    text-decoration: none;
    color: #f5f5f5;
    color: var(--text-color);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 30px;
    top: -50%;
    transition: 850ms;
}

.navbar-search .search-box {
    padding: 10px;
    border-radius: 6px;
    border: none;
    outline: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar-search .search-box:focus {
    border: 1px solid #632ce4;
    border: var(--border);
}

.navbar-search .btn-submit {
    color: #632ce4;
    color: var(--bg-btn);
    margin-left: 5px;
    padding: 10px;
    border-radius: 6px;
    border: 1px solid #632ce4;
    border: 1px solid var(--bg-btn);
    background-color: #060b26;
    background-color: var(--bg);
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar-search .btn-submit:hover {
    color: #f5f5f5;
    color: var(--text-color);
    border: 1px solid #632ce4;
    border: 1px solid var(--bg-btn);
    background-color: #632ce4;
    background-color: var(--bg-btn);
}


.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #060b26;
    background-color: var(--bg);
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    overflow: auto;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-menu-items {
    width: 100%;
}

.nav-toggle {
    background-color: #060b26;
    background-color: var(--bg);
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: start;
}

span {
    margin-left: 16px;
}


@media screen and (max-width: 1024px) {
    .navbar-logo {
        font-size: 1.5rem;
    }

    .user-profile {
        font-size: 1.5rem;
    }

    .navbar-search {
        position: absolute;
        top: 0;
        right: -100%;
        transition: 850ms;
    }

    .navbar-search-1 {
        font-size: 2rem;
        position: absolute;
        top: 30%;
        right: 10%;
        transition: 850ms;
    }

}

@media screen and (max-width: 768px) {
    .navbar-logo {
        font-size: 1rem;
    }

    .user-profile {
        font-size: 1.5rem;
    }

    .navbar-search {
        position: absolute;
        top: 0;
        right: -100%;
        transition: 850ms;
    }

    .navbar-search-1 {
        position: absolute;
        top: 30%;
        right: 10%;
        transition: 850ms;
    }

}

@media screen and (max-width: 512px) {
    .navbar-logo {
        font-size: .9rem;
    }

    .user-profile {
        font-size: 1rem;
    }

    .navbar-search {
        position: absolute;
        top: 0;
        right: -100%;
        transition: 850ms;
    }

    .navbar-search-1 {
        font-size: 1.2rem;
        position: absolute;
        top: 40%;
        right: 10%;
        transition: 850ms;
    }

}
:root {
    --bg: #060b26;
    --bg-accent: #1b2246;
    --text-color: #f5f5f5;
    --nav-size: 60px;
    --border: 1px solid #632ce4;
    --border-radius: 8px;
    --speed: 500ms;
    --bg-btn: #632ce4;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    color: #f5f5f5;
    color: var(--text-color);
    text-decoration: none;
    ;
}

.drop-item {
    width: calc(60px * 0.8);
    width: calc(var(--nav-size) * 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Icon Button */
.icon-left {
    --button-size: calc(var(--nav-size) * 0.5);
    width: var(--button-size);
    height: var(--button-size);
    background-color: #484a4d;
    border-radius: 50%;
    padding: 5px;
    margin: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: -webkit-filter 300ms;
    transition: filter 300ms;
    transition: filter 300ms, -webkit-filter 300ms;
    font-family: 'Lato', sans-serif;
}

.icon-left:hover {
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
}

.icon-left svg {
    fill: #f5f5f5;
    fill: var(--text-color);
    width: 18px;
    height: 18px;
}


/* Dropdown Menu */

.dropdown {
    position: absolute;
    top: 58px;
    width: 300px;
    height: auto !important;
    -webkit-transform: translateX(45%);
            transform: translateX(45%);
    background-color: #1b2246;
    background-color: var(--bg-accent);
    border: 1px solid #632ce4;
    border: var(--border);
    border-radius: 8px;
    border-radius: var(--border-radius);
    padding: 1rem;
    overflow: hidden;
    transition: height 500ms ease;
    transition: height var(--speed) ease;
}

.menu {
    width: 100%;
}

.menu-item {
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    border-radius: var(--border-radius);
    transition: background 500ms;
    transition: background var(--speed);
    padding: 0.5rem;
    font-size: 15px;
}

.menu-item .icon-left {
    margin-right: 0.5rem;
}


.menu-item .icon-left:hover {
    -webkit-filter: none;
            filter: none;
}

.menu-item:hover {
    background-color: #632ce4;
    background-color: var(--bg-btn);
}


/* CSSTransition classes  */
.menu-primary-enter {
    position: absolute;
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
}

.menu-primary-enter-active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
}

.menu-primary-exit {
    position: absolute;
}

.menu-primary-exit-active {
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
}


.menu-secondary-enter {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
}

.menu-secondary-enter-active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
}

.menu-secondary-exit-active {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
}

